a {
    text-decoration: none;
}
ul,ol,li {
    list-style: none;
}
img {
    vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: inherit;
    font-size: inherit;
}


 /* 
 бургер
  */
  .wrapper {
    width: 100%;
    min-height: 90px;
  }

  .header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    background: #1d1c1c;
}
.header__container{
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    padding: 0px 30px;
    align-items: center;
    justify-content:space-between;
    min-height: 40px;
}
.header__logo{
    border-radius: 50%;    
    position: relative;   
    z-index: 5;    
    color: rgb(255, 255, 255);
    font-size: 24px;
    font-family:Verdana, Geneva, Tahoma, sans-serif
}

.header__logo:hover{
    border-radius: 50%;    
    position: relative;   
    z-index: 5;
    width: 40px;
    height: 40px;
    color: rgb(227, 245, 230);
    font-size: 24px;
}

.menu__icon{
    display: none;
}

.menu__link{
    color: #fff;
    font-size: 18px;
}
.menu__link:hover{
    color:rgb(148, 148, 148);
    font-size: 18px;
}
.menu__list > li{
    position: relative;
    margin: 0px 0px 0px 10px;
}
.menu__sub-list{
    position: absolute;
    top: 70%;
    right: 0;
    background-color: rgb(80, 82, 76);
    padding: 10px;
    min-width: 300px;
    border-radius: 0px 0px 20px 20px;
}
.menu__sub-list li{
    margin: 0px 0px 10px 0px;
}
.menu__sub-list li:last-child{
    margin: 0;
}
.menu__sub-link{
    color: #fff;
}
.menu__sub-link:hover{
    text-decoration: underline;
}
.menu__arrow{
    display: none;
    position: relative;
    left: 15px;

}
._touch .menu__list > li {
    display: flex;
    align-items: center;
}
._touch .menu__link {
    flex: 1 1 auto;
}
._touch .menu__arrow {
    display: block;
    width: 0;
    height: 0;
    margin-right: 5px;
    transition: transform 1s ease 0s;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 10px solid #fff;
}

/* ---------------------------- */
._touch .menu__list > li._active .menu__sub-list {
    opacity: 1;
    visibility: visible;
    transform: translate(0px 0px);
    pointer-events: all;
}

._touch .menu__list > li._active .menu__arrow {
    transform: rotate(-180deg);
}

._pc .menu__list > li:hover .menu__sub-list {
    opacity: 1;
    visibility: visible;
    transform: translate(0px 0px);
    pointer-events: all;
} 



/* ---------------------------- */
 @media (min-width: 767px) {
    .menu__list {
        display: flex;
        align-items: center;
        min-height: 50px;       
        margin-bottom: 0px;
        
    }
    .menu__list > li {
        margin-left: 30px;
    }
    .menu__sub-list {
        transform: translate(0,10%);
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        transition: all 0.3s ease 0s;
    }
}

@media (max-width: 767px){
    .menu__icon {
        z-index: 5;
        display: block;
        position: relative;
        width: 30px;
        height: 18px;
        cursor: pointer;
    }
    .menu__icon span,
    .menu__icon::before,
    .menu__icon::after {
        left: 0;
        position: absolute;
        height: 10%;
        width: 100%;
        transition: all 0.3s ease 0s;
        background-color: #fff;
    }
    .menu__icon::before,
    .menu__icon::after {
        content: '';
    }
    .menu__icon::before{
        top:0;
    }
    .menu__icon::after{
        bottom: 0;
    }
    .menu__icon span {
        top: 50%;
        transform: scale(1) translate(0px, 50%);
    }

    .menu__icon._active span {
        transform: scale(0) translate(0px, -50px);
    }
    .menu__icon._active::before {
        top: 50%;
        transform: rotate(-45deg) translate(0px, -50%);
    }
    .menu__icon._active::after {
        top: 50%;
        transform: rotate(45deg) translate(0px, 50%);
    }

    .menu__body {
        position: fixed;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background-color: rgba(187, 184, 184, 0.9);
        padding: 100px 30px 30px 30px;
        transition: all all 0.3s ease 0s;
        overflow: auto;
    }
    .menu__body._active {
        left: 0;
    }
    .menu__body::before {
        content: "";
        position: fixed;
        width: 100%;
        top: 0;
        left: 0;
        height: 50px;
        background-color: #020202;
        z-index: 2;
    }
    .menu__list > li {
        flex-wrap: wrap;
        margin: 0px 0px 30px 0px;
    }
    .menu__list > li:last-child {
        margin-bottom: 0;
    }
    .menu__list > li._active .menu__sub-list {
        display: block;
    }
    .menu__link {
        font-size: 16px;
    }
  
    .menu__sub-list {
        position: relative;
        background-color: rgb(131, 130, 130);
        flex: 1 1 100%;
        margin: 20px 0px 0px 0px;
        display: none;
    }
    .menu__sub-link {
        font-size: 20px;
        color: #000;
    }
}



