body {
  font-family: 'Nanum Gothic', sans-serif;
}

footer {
  height: 100px;
}

header {
  margin-bottom: 50px;
}

.header-text{
  font-size: 24px;
}

.container {
  width: 80%;
}

.hide {
  display: none;
}

.border{
  min-height: 130px;
  width: auto;
  background: rgb(255, 255, 255);
  border: 5px double rgb(124, 124, 124) !important;   
}

.out{
  font-family: 'Otomanopee One', sans-serif;
  color: rgb(56, 172, 33);
}

.index-arr{
  padding: 3px; 
  border-radius: 30%;
  color: rgb(179, 179, 179);
  display: block;
}

.item-arr{
  display: inline-block;
  padding: 0px 5px;
  color: rgb(7, 7, 7);
  background: rgba(165, 163, 163, 0.562);
  border-radius: 10px;
  min-width: 40%;
}

.block-item{
  margin-bottom: 10px;
}

.bold {
  font-weight: bold;
} 	

.image-ava{
  display: inline-block;
  background-image: url(./images/pic000.jpg);
  width: 300px;
  height: 300px;
  background-size:cover;
  border-radius: 20px;
  text-align: center;
}

.image-ava:hover{
  display: block;
  background-image: url(./images/pic001.jpg);
  width: 300px;
  height: 300px;
  background-size:cover;
  border-radius: 20px;
  text-align: center;
}

@media (max-width: 990px) {
  .header-text{
    margin-top: 20px;
  }
}

@media (max-width: 1200px) {
  .container {
    width: 100%;
  }
}

@media (max-width: 766px) {
  .container {
    width: 100%;
  }
}


