 .cart-component {
  display: flex;
  justify-content: space-around;
  font-family: Arial, Helvetica, sans-serif;
} 

.title-name {
  font-size: 24px;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: rgb(27, 21, 48);
  text-align: center;
}

.cart-list {
  width: 40%;
}

.good-list {
  width: 60%;
}


.goods-field {  
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.goods-field::after {
    content: "";
    display: block;
    clear: both;
}

.goods-block {
  margin: 0px 10px 10px 10px;
  display: block;
  float: left;
  background:  rgb(235, 240, 230);
  padding: 20px;
  border-radius: 10px;  
  text-align: center;
  flex: 0 0 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 150px;
}

.goods-block__title {  
  padding: 0 0 0 0;
  text-transform: lowercase;
  color: rgb(126, 111, 111);
  letter-spacing: 1px;
  flex: 1 1 auto;
}

.goods-block__cost {
  font-size: 16px;
  color:rgb(34, 126, 11);  
  font-weight: 700;
  flex: 0 1 auto;
}

table {
width: 100%;
  border-collapse: collapse;
}

table td, table th { 
  padding: 5px;
  text-align: center;
}

table .table-thead {
  text-transform: uppercase;
  color: rgb(255, 255, 255);
  background: rgb(168, 162, 162);
  border-radius: 10px;
}

img {
  width: 64px;
  height: 64px;
}

.img {
  width: 20px;
  height: 20px;
}

.add-to-cart {
  width: 100px;
  height: 25px;
  background: rgb(70, 145, 231);
  border: none;
  color: #fff;
  font-weight: 700;
  letter-spacing: 0.2px;
  text-transform: lowercase;
  border-radius: 10px;
  cursor: pointer;
  flex: 0 0 auto;
}

.add-to-cart:hover {  
  background: rgb(231, 167, 70);
}


.min-add-to-cart {
  color:#fff;
  background: rgb(13, 99, 17);
  border-radius: 100%;
  border: none;
  padding: 5px;
  width: 30px;
  height: 30px;
  font-size: 10px;
}

.min-add-to-cart:hover {
  background: rgb(27, 117, 31);  
}

.min-del-to-cart {
  color:#fff;
  background: rgb(153, 52, 39);
  border-radius: 100%;
  border: none;
  padding: 5px;
  width: 30px;
  height: 30px;
  font-size: 10px;
}

.min-del-to-cart:hover { 
  background: rgb(97, 50, 44);  
}

tr {
  border-bottom: 1px solid grey;
}

.table-block {
  text-align: right;
}

.total-coast {
  margin: 30px 0px 0px 0px;
  font-size: 16px;
  color: rgb(102, 94, 94);
  text-transform: uppercase;
  letter-spacing: 1px;
  display: inline-block;
}

